@import url(https://fonts.googleapis.com/css?family=Cairo);
body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  direction: rtl;
  width: 1200px;
  font-family: 'Cairo', sans-serif;
  text-align: right;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (min-width: 2000px) {
  body {
    width: 2000px;
  }
  .response-box {
    width: 800px !important;
  }
}

@media only screen and (max-width: 1200px) {
  body {
    width: 1000px;
  }
}
@media only screen and (max-width: 1000px) {
  body {
    width: 800px;
  }
  .response-box {
    width: 500px !important;
  }
  .side-bar {
    display: none;
  }
  .main-content {
    grid-template-columns: 1fr;
    grid-gap: 0px;
    margin: 0 auto;
  }
  .account,
  .exam-wrapper,
  .exam-informations,
  .exam-body {
    width: 800px !important;
  }
}

@media only screen and (max-width: 800px) {
  body {
    width: 400px;
  }
  .response-box {
    width: 300px !important;
  }
  .account,
  .exam-wrapper,
  .exam-informations,
  .exam-body {
    width: 400px !important;
  }
}

.nav {
  display: grid;
  grid-template-columns: 1fr 2fr;
  background: #EEEEEE;
  color: #212121;
}

.nav h2 {
  padding: 5px 50px;
}

.nav .menu {
  direction: ltr;
  float: left;
}

.nav .menu ul {
  list-style: none;
  margin: 0;
}

.nav .menu ul a {
  display: block;
}

.nav .menu ul a li {
  float: left;
  text-decoration: none;
  margin: 0px 10px;
  padding: 33px;
  overflow: hidden;
  color: #212121;
  -webkit-transition: ease-in 0.3s;
  transition: ease-in 0.3s;
}

.nav .menu ul a li:hover {
  background: black;
  color: white;
}
/*# sourceMappingURL=Header.css.map */
.main {
  width: 100%;
}
/*# sourceMappingURL=MainApp.css.map */
.module-exams {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
}

.module {
  margin: 20px 0px;
}
/*# sourceMappingURL=Module.css.map */
.exam-wrapper {
  padding: 20px 10px;
}
/*# sourceMappingURL=ExamWrapper.css.map */
.side-bar {
  background: #EEEEEE;
}

.side-bar h4 {
  text-align: center;
  margin: 10px 0px;
}

.side-bar .list-group {
  padding-right: 0px !important;
}

.side-bar .list-group li {
  float: right;
}

.side-bar .list-group li a {
  text-decoration: none;
  color: #212121;
  -webkit-transition: ease-in 0.2s;
  transition: ease-in 0.2s;
}

.side-bar .list-group li a:hover {
  color: #6e6e6e;
}
/*# sourceMappingURL=SidePage.css.map */
.exam-informations {
  padding: 20px 10px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}
/*# sourceMappingURL=ExamInformations.css.map */
.account-informations {
  text-align: center;
  padding: 20px 10px;
}

.avatar {
  position: relative;
  width: 100px;
  height: 100px;
  top: -30px;
  margin: 0 auto;
}
/*# sourceMappingURL=AccountInformations.css.map */
.info-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.info-section .prop {
  text-align: left;
}

.info-section .prop-value {
  float: right;
  text-align: right;
}
