.account {
  width: 700px;
  margin: 60px auto;
}
/*# sourceMappingURL=Account.css.map */
.profile-settings {
  width: 800px;
  margin: 50px auto;
}
/*# sourceMappingURL=ProfileSettings.css.map */
.informations-input {
